@use 'base/variables' as v;
@use 'base/mixins' as m;
.barra-mobile
{
    background-color: v.$naranja-oscuro;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .imagen-escuela-mobile
    {
        img
        {
        max-width: 30rem;
        }
    }
    @include m.tablet 
    {
      display: none;
    }
    h1
    {
        color: v.$blanco;
        margin: 0;
    }
    .menu
    {
        img
        {
            width: .8rem;
            &:hover
            {
                cursor: pointer;
            }
        }
    }
}
.barra 
{
    display: none;
    @include m.tablet
    {
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, .4);
        text-align: center;  
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    p
    {
        margin: 0;
        font-size: 1.4rem;
        span 
        {
            font-weight: 700;
            color: v.$rojo-oscuro;
        }
    }

    .cerrar-sesion
    {
        @include m.boton( v.$rojo-oscuro, v.$blanco);
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: 1.2rem;
    }

}